import React from 'react';

const Locale = 'pl-PL';

interface MoneyProps {
  value: {
    value: number;
    currency: string;
  };
}

export const Money: React.FC<MoneyProps> = ({ value: { value, currency } }) => {
  return (
    <React.Fragment>
      {value.toLocaleString(Locale, { style: 'currency', currency: currency, useGrouping: true })}
    </React.Fragment>
  );
};
