export interface BucketResponse {
  id: string;
  icon: string;
  name: string;
  amount: Money;
}

export interface Money {
  value: number;
  currency: string;
}

class BucketsRepository {
  findAll(): readonly BucketResponse[] {
    return Object.values(db);
  }

  getById(id: string) {
    // @ts-ignore
    return db[id] as BucketResponse;
  }

  save(bucket: BucketResponse) {
    // @ts-ignore
    db[bucket.id] = bucket;
  }

  add(id: string, amount: number): BucketResponse {
    const bucket = this.getById(id);
    if (bucket) {
      bucket.amount.value += amount;
      return { ...bucket };
    } else {
      throw 'Cannot find bucket';
    }
  }

  withdraw(id: string, amount: number): BucketResponse {
    const bucket = this.getById(id);
    if (bucket) {
      bucket.amount.value -= amount;
      return { ...bucket };
    } else {
      throw 'Cannot find bucket';
    }
  }
}

const BucketsRepositoryInstance = new BucketsRepository();

export { BucketsRepositoryInstance as BucketsRepository };
