import React, { FC, useState } from 'react';
import { BucketResponse } from './BucketsRepository';
import {
  Accordion,
  AccordionActions,
  AccordionDetails,
  AccordionSummary,
  Avatar,
  Box,
  Button,
  Collapse,
  Divider,
  InputAdornment,
  makeStyles,
  TextField,
  Typography,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Money } from '../components/Money';
import { AddCircleOutline, CancelOutlined, CheckCircleOutlined, RemoveCircleOutline } from '@material-ui/icons';

const useStyles = makeStyles(theme => ({
  root: {
    width: '96%',
    marginLeft: '2%',
    marginRight: '2%',
    marginTop: '2%',
    marginBottom: '4%',
  },
  card: {
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  icon: {
    marginRight: '1em',
  },
  name: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexGrow: 1,
    flexShrink: 0,
  },
  amount: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

interface BucketsListProps {
  buckets: BucketResponse[];
  onWithdraw: (id: string, amount: number) => void;
  onAdd: (id: string, amount: number) => void;
}

export const BucketsList: React.FC<BucketsListProps> = ({ buckets, onWithdraw, onAdd }) => {
  const classes = useStyles();
  const [expanded, setExpanded] = useState('');

  return (
    <div className={classes.root}>
      {buckets.map(bucket => (
        <Bucket
          key={bucket.id}
          bucket={bucket}
          expanded={expanded === bucket.id}
          onExpand={id => setExpanded(id)}
          onHideDetails={() => setExpanded('')}
          onAdd={onAdd}
          onWithdraw={onWithdraw}
        />
      ))}
    </div>
  );
};

interface BucketProps {
  bucket: BucketResponse;
  expanded: boolean;
  onExpand: (id: string) => void;
  onHideDetails: (id: string) => void;
  onWithdraw: (id: string, amount: number) => void;
  onAdd: (id: string, amount: number) => void;
}

const Bucket: FC<BucketProps> = ({
  bucket: { id, name, icon, amount },
  expanded,
  onExpand,
  onHideDetails,
  onWithdraw,
  onAdd,
}) => {
  const classes = useStyles();

  const onChange = (expand: boolean) => {
    if (expand) {
      onExpand(id);
    } else {
      onHideDetails(id);
    }
  };

  const handleAdd = (amount: number) => {
    onAdd(id, amount);
  };

  const handleWithdraw = (amount: number) => {
    onWithdraw(id, amount);
  };

  return (
    <Accordion expanded={expanded} onChange={(_, expand) => onChange(expand)}>
      <AccordionSummary expandIcon={<ExpandMoreIcon />} classes={{ content: classes.card }}>
        <Avatar className={classes.icon} alt={name} title={name}>
          {icon}
        </Avatar>
        <Typography className={classes.name}>{name}</Typography>
        <Typography className={classes.amount}>
          <Money value={amount} />
        </Typography>
      </AccordionSummary>
      <BucketActions onWithdraw={handleWithdraw} onAdd={handleAdd} />
    </Accordion>
  );
};

type AvailableBucketActions = 'add' | 'withdraw' | 'default';

interface BucketActionsProps {
  onWithdraw: (amount: number) => void;
  onAdd: (amount: number) => void;
}

const BucketActions: FC<BucketActionsProps> = ({ onAdd, onWithdraw }) => {
  const [action, setAction] = useState<AvailableBucketActions>('default');

  const setDefaultAction = () => setAction('default');

  const handleAdd = (amount: number) => {
    onAdd(amount);
    setDefaultAction();
  };

  const handleWithdraw = (amount: number) => {
    onWithdraw(amount);
    setDefaultAction();
  };

  return (
    <>
      <Collapse in={action === 'add'}>
        <AddToBucket onCancel={setDefaultAction} onAdd={handleAdd} />
      </Collapse>
      <Collapse in={action === 'withdraw'}>
        <WithdrawFromBucket onCancel={setDefaultAction} onWithdraw={handleWithdraw} />
      </Collapse>
      <Collapse in={action === 'default'}>
        <BucketActionButtons onChoice={setAction} />
      </Collapse>
    </>
  );
};

interface BucketActionButtonsProps {
  onChoice: (action: AvailableBucketActions) => void;
}

const BucketActionButtons: FC<BucketActionButtonsProps> = ({ onChoice }) => {
  return (
    <>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onChoice('withdraw')}>
          <RemoveCircleOutline /> Wypłać
        </Button>
        <Button size="small" color="primary" onClick={() => onChoice('add')}>
          <AddCircleOutline /> Dodaj
        </Button>
      </AccordionActions>
    </>
  );
};

interface AddToBucketProps {
  onAdd: (amount: number) => void;
  onCancel: () => void;
}

const AddToBucket: FC<AddToBucketProps> = ({ onAdd, onCancel }) => {
  const [error, setError] = useState(false);
  const [amount, setAmount] = useState('');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setError(+value < 0);
    setAmount(value);
  };

  return (
    <>
      <AccordionDetails>
        <TextField
          required
          error={error}
          label="Ile?"
          type="number"
          value={amount}
          onChange={handleChange}
          InputProps={{
            endAdornment: <InputAdornment position="end">zł</InputAdornment>,
          }}
        />
      </AccordionDetails>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onCancel()}>
          <CancelOutlined /> Anuluj
        </Button>
        <Button size="small" color="primary" disabled={amount === '' || error} onClick={() => onAdd(+amount)}>
          <CheckCircleOutlined /> Zatwierdź
        </Button>
      </AccordionActions>
    </>
  );
};

interface WithdrawFromBucketProps {
  onWithdraw: (amount: number) => void;
  onCancel: () => void;
}

const WithdrawFromBucket: FC<WithdrawFromBucketProps> = ({ onWithdraw, onCancel }) => {
  const [error, setError] = useState(false);
  const [amount, setAmount] = useState('');
  const [reason, setReason] = useState('');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setError(+value < 0);
    setAmount(value);
  };

  const handleChangeReason = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    setReason(value);
  };

  return (
    <>
      <AccordionDetails>
        <form noValidate autoComplete="off">
          <Box flexDirection="column">
            <TextField
              error={reason === ''}
              required
              label="Na co?"
              type="text"
              value={reason}
              onChange={handleChangeReason}
            />

            <TextField
              required
              error={error}
              label="Ile?"
              type="number"
              value={amount}
              onChange={handleChange}
              InputProps={{
                endAdornment: <InputAdornment position="end">zł</InputAdornment>,
              }}
            />
          </Box>
        </form>
      </AccordionDetails>
      <Divider />
      <AccordionActions>
        <Button size="small" color="secondary" onClick={() => onCancel()}>
          <CancelOutlined /> Anuluj
        </Button>
        <Button
          size="small"
          color="primary"
          disabled={amount === '' || reason === '' || error}
          onClick={() => onWithdraw(+amount)}
        >
          <CheckCircleOutlined /> Zatwierdź
        </Button>
      </AccordionActions>
    </>
  );
};
