import React, { FC, useEffect, useState } from 'react';
import { BucketResponse, BucketsRepository } from './BucketsRepository';
import { BucketsList } from './BucketsList';

export const Buckets: FC = () => {
  const [buckets, setBuckets] = useState([] as BucketResponse[]);

  useEffect(() => {
    const result = BucketsRepository.findAll();
    setBuckets([...result]);
  }, []);

  const handleAdd = (id: string, amount: number) => {
    const result = BucketsRepository.add(id, amount);
    const newBuckets = [...buckets];
    const index = newBuckets.findIndex(b => b.id === result.id);
    newBuckets[index] = result;
    setBuckets(newBuckets);
    return { ...result };
  };

  const handleWithdraw = (id: string, amount: number) => {
    const result = BucketsRepository.withdraw(id, amount);
    const newBuckets = [...buckets];
    const index = newBuckets.findIndex(b => b.id === result.id);
    newBuckets[index] = result;
    setBuckets(newBuckets);
    return { ...result };
  };

  return <BucketsList buckets={buckets} onAdd={handleAdd} onWithdraw={handleWithdraw} />;
};
